.app__gallery {
  display: flex;
  flex-direction: row;
  background: $color-black;
  padding: 4rem 0 4rem 6rem;

  &-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    min-width: 500px;
    padding-right: 2rem;

    & > p {
      color: #aaa;
      margin-top: 2rem;
    }

    & > button {
      margin-top: 1rem;
    }
  }

  &-img {
    flex: 1;
    display: flex;
    flex-direction: row;
    max-width: 50%;
    position: relative;

    &_container {
      display: flex;
      flex-direction: row;
      width: max-content;
      overflow-x: scroll;
      -ms-overflow-style: none;
      scrollbar-width: none;

      &::-webkit-scrollbar {
        display: none;
      }
    }

    &_card {
      position: relative;
      min-width: 301px;
      height: 447px;
      margin-right: 2rem;

      & > img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        opacity: 1;
        transition: 0.5s ease;
      }

      & > img:hover {
        opacity: 0.35;
      }
    }
    &-icon {
      position: absolute;
      color: #fff;
      font-size: 2rem;
      opacity: 0.5;
      transition: 0.5s ease;
      cursor: pointer;
    }

    &_arrow {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 1rem;
      position: absolute;
      bottom: 5%;

      &-icon {
        color: #fff;
        font-size: 2rem;
        cursor: pointer;
        background-color: $color-black;
        border-radius: 5px;

        &:hover {
          color: $color-golden;
        }
      }
    }
  }
}

.app__gallery-img_card:hover .app__gallery-img-icon {
  opacity: 1;
}

@media screen and (min-width: 2000px) {
    .app__gallery-content{
        //min-width: 1000px;
        padding-right: 4rem;
    }
    .app__gallery-img_card{
        min-width: 400px;
        height: 550px;
    }
}

@media screen and (max-width: 1150px) {
    .app__gallery{
        flex-direction: column; 
    }

    .app__gallery-img{
        max-width: 100%;
        margin: 5rem 0;
    }
}
@media screen and (max-width: 850px) {
    .app__gallery{
        padding: 4rem 0 4rem 2rem;
    }
}
@media screen and (max-width: 650px) {
   .app__gallery{
    padding: 4rem 0 4rem 2rem;
   } 
   .app__gallery-content{
    min-width: 100%;
   }
   .app__gallery-img_card{
    min-width: 240px;
    height: 320px;
}
}