.app__specialMenu {
  flex-direction: column;
  background: $color-black;

  &-title {
    margin-bottom: 2rem;
    text-align: center;
  }

  &-menu {
    width: 100%;
    margin: 2rem 0;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row;

    &-wine,
    &-cocktails {
      flex: 1;
      width: 100%;
      flex-direction: column;

      & > h2 {
        font-family: $font-base;
        font-weight: 600;
        color: $color-white;
        font-size: 45px;
        line-height: 58.8px;
        letter-spacing: 0.4em;
      }
    }

    &_img {
      width: 410px;
      margin: 0 2rem;

      & > img {
        width: 100%;
        height: auto;
      }
    }
  }

  &_menu_items {
    display: flex;
    flex-direction: column;
    margin: 2rem 0;
    width: 100%;
  }
}
@media screen and (max-width: 2000px) {
    .app__specialMenu-menu_img {
        width: 650px;

        & >img{
            height: 920px;
        }
      }
  
}

@media screen and (max-width: 1150px) {
  .app__specialMenu-menu {
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  .app__specialMenu-menu_img {
    margin: 3rem 0;
  }
}

@media screen and (max-width: 650px) {
  .app__specialMenu-menu_img {
    width: 100%;
  }
  .app__specialMenu-menu-wine > h2,
  .app__specialMenu-menu-cocktails > h2 {
    font-size: 35px;
    line-height: 48.5px;
  }
}
