.app__navbar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: $color-black;
  padding: 1rem 2rem;
  color: $color-white;

  &-logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    & > img {
      width: 150px;
    }
  }

  &-links {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;

    & > li {
      margin: 0 1rem;
      cursor: pointer;
    }

    & > li:hover {
      color: $color-grey;
    }
  }

  &-login {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    & > a {
      margin: 0 1rem;
      text-decoration: none;
      transition: 0.5s ease;
    }

    & > a:hover {
      border-bottom: 1px solid $color-golden;
    }

    & > div {
      width: 1px;
      height: 30px;
      background-color: $color-grey;
    }
  }

  &-smallscreen {
    display: none;

    &_GiHamburgerMenu {
      font-size: 27px;
      color: $color-white;
    }

    &_overlay {

        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background: $color-black;
        transition: 0.5s ease;
        flex-direction: column;
        z-index: 5;

      &_MdOutlineRestaurantMenu {
        font-size: 27px;
      }
    }

    &_links{
        list-style: none;

        & > li{
            margin: 2rem;
            cursor: pointer;
            color: $color-golden;
            font-size: 2rem;
            text-align: center;
            font-family: $font-base;
            
        }

        & > li:hover{
            color: $color-white;
        }
    }
  }
}

.app__navbar-smallscreen_overlay .overlay_MdOutlineRestaurantMenu{
    font-size: 27px;
    color: $color-golden;
    cursor: pointer;

    position: absolute;
    top: 20px;
    right: 20px;
}

@media  screen and (max-width: 2000px) {
    .app__navbar-logo > img{
        width: 210px;
    }
}

@media  screen and (max-width: 1150px) {
    .app__navbar-links{
        display: none;
    }

    .app__navbar-smallscreen{
        display: flex;
    }
}

@media  screen and (max-width: 650px) {
    .app__navbar{
        padding: 1rem;
    }

    .app__navbar-login{
        display: none;
    }

    .app__navbar-logo > img{
        width: 110px;
    }
}
